      menuEnd: false,
      time2: null,
      menu2: false,
      time3: null,
      menu3: false,
      isBanner: false,
    };
  },

  methods: {
    showDialog(string, status) {
      this.dialog = true;
      this.dialogAdd = false;
      this.titleDialog = string;
      if (status === true) {
        this.isBanner = true
      }
      if (status === false) {
        this.isBanner = false
      }
    },
    addBanner() {
      this.dialog = false;
      if (this.isBanner) {
        this.dialogAdd = true;
      }
    },
    closeAddDialog() {
      this.dialog = false;
      this.dialogAdd = false;
    },
    closeDialog() {
      this.dialog = false;
      this.dialogAdd = false;
    },
  },
};
</script>
